import React, { useEffect, useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'react-toastify/dist/ReactToastify.css';
import './main.css';
import {
    Container, Row, Col, Card, CardHeader, CardFooter, CardBody,
    Breadcrumb,Modal, ModalHeader, ModalBody, Button
} from 'reactstrap';
import { toast } from 'react-toastify';
export const MainComponent = (props) => {
    const [prod, setprod] = useState([])
    const [total, settotal] = useState(130)
    const [quantity, setquantity] = useState(4)
    const [mopen, setmopen] = useState(false)
    useEffect(() => {
        if (localStorage.getItem("checkoutdta") == null) {
            const maindata = [
                {
                    "item": "pr-01",
                    "quantity": 1,
                    "price": 25
                },
                {
                    "item": "pr-02",
                    "quantity": 1,
                    "price": 30
                },
                {
                    "item": "pr-03",
                    "quantity": 1,
                    "price": 35
                },
                {
                    "item": "pr-04",
                    "quantity": 1,
                    "price": 40
                }
            ]
            localStorage.setItem("checkoutdta", JSON.stringify(maindata));
            setprod(maindata);
        } else {
            if (JSON.stringify(prod) !== localStorage.getItem("checkoutdta")) {
                setprod(JSON.parse(localStorage.getItem("checkoutdta")));
                setquantity(Number(localStorage.getItem("quan")));
                settotal(Number(localStorage.getItem("total")));
            }
            // else if{

            // }
        }
    }, [prod]);
    useEffect(() => {
        localStorage.setItem("quan",quantity);
        localStorage.setItem("total",total);
        if (quantity == 0) {
            setmopen(true)
        }
    }, [quantity])
    const calprice = function (cp) {
        settotal()
    }
    const addQuantity = function (id) {
        const idx = id.split("a-")[1];
        var pr = prod;
        var details = pr[idx];
        details.quantity = details.quantity + 1;
        pr[idx] = details;
        setprod(pr);
        setquantity(quantity + 1)
        settotal(total + details.price)
        localStorage.setItem("checkoutdta", JSON.stringify(pr));
    }
    const minusQuantity = function (id) {
        const idx = id.split("m-")[1];
        var pr = prod;
        var details = pr[idx];
        if(details.quantity==0)
        {
            toast.error("the quatity is zero");
        }else{
            details.quantity = details.quantity - 1;
            pr[idx] = details;
            setprod(pr);
            setquantity(quantity - 1);
            settotal(total - details.price);
            localStorage.setItem("checkoutdta", JSON.stringify(pr));
        }
    }
    const removeProduct = function (id) {
        const idx = id.split("r-")[1];
        var pr = prod;
        var details = pr[idx];
        var cur_qua = details.quantity;
        var totalprice = cur_qua * details.price;
        details.quantity = 0;
        const index = prod.indexOf(idx);
        if (idx > -1) {
            pr.splice(idx, 1);
        }
        //pr[idx] = details;
        setprod(pr);
        settotal(total - totalprice);
        setquantity(quantity - cur_qua);
        toast("Item Removed From Cart")
        var x = document.getElementById(`item-${details.item}`);
        x.style="display:none"
        localStorage.setItem("checkoutdta", JSON.stringify(prod));
    }
    const closemodal = ()=>{
        setmopen(false);
        
    }
    const reloaddata = ()=>{
        setmopen(false);
        localStorage.removeItem("checkoutdta");
        setprod([]);
        settotal(130);
        setquantity(4);
    }
    return (
        <Container>
            <MyModal isopen={mopen} close={closemodal} reload={reloaddata}></MyModal>
            <Row className="page-main">
                <Col xs="8" className="product-left">
                    {prod.map(
                        (dta, index) => {
                            return (
                                <Breadcrumb id={`item-${dta.item}`}>
                                    <Row className="item-row" id={`items-${index}`}>
                                        <Col xs="3">{dta.item}</Col>
                                        <Col xs="3">
                                            <i role="button" id={`a-${index}`}
                                                onClick={e => {
                                                    e.preventDefault()
                                                    addQuantity(e.target.id);
                                                }}
                                            >+</i>{dta.quantity}
                                            <i role="button" id={`m-${index}`}
                                                onClick={e => {
                                                    e.preventDefault()
                                                    minusQuantity(e.target.id);
                                                }}
                                            >-</i></Col>
                                        <Col xs="3" id={`p-${index}`}>{dta.price}</Col>
                                        <Col xs="3"><i role="button" id={`r-${index}`}
                                            onClick={e => {
                                                e.preventDefault()
                                                removeProduct(e.target.id);
                                            }}
                                        >X</i></Col>
                                    </Row>
                                </Breadcrumb>
                            )
                        }
                    )
                    }
                </Col>
                <Col xs="4" className="product-right">
                    <Card>
                        <CardHeader>Total</CardHeader>
                        <CardBody>
                            <Row>
                                <Col xs="5">{`Items (${quantity})`}</Col>
                                <Col xs="7">{total}</Col>
                            </Row>
                            <Row>
                                <Col xs="5">Discount</Col>
                                <Col xs="7">10%</Col>
                            </Row>
                            <Row>
                                <Col xs="5">TypeDiscount</Col>
                                <Col xs="7">20%</Col>
                            </Row>
                        </CardBody>
                        <CardFooter>
                            <Row>
                                <Col xs="4">Total</Col>
                                <Col xs="8">{total == 0 ? 0 : total + 3}</Col>
                            </Row>
                        </CardFooter>
                    </Card>
                </Col>
            </Row>
        </Container>
    )

}

const MyModal = (props) => {
    return (
        <Modal isOpen={props.isopen}>
            <ModalHeader toggle={props.close}>Alert</ModalHeader>
            <ModalBody>
                <Row>
                    <h2>Reload deafult cart</h2>
                </Row>
                <Row>
                    <Button className="okbtn" onClick={e=>{
                        props.reload()
                    }}>yes</Button>
                    <Button onClick={e=>{
                        props.close()
                    }}>no</Button>
                </Row>
                    
            </ModalBody>
        </Modal>
    )
}